<template>
  <div class="integral ios-s">
    <app-header show-back="true" title="积分规则"></app-header>
    <div class="cont">
      <div class="item">
        <h3>重卡汇积分是什么?</h3>
        <p>积分是重卡汇为重卡汇的卡友们发放的一种优惠福利。重卡汇用户可使用积分直接抵扣订单的付费金额，也可以使用积分参与礼品兑换或大转盘抽奖等福利活动。</p>
      </div>
      <div class="item">
        <h3>如何获得重卡汇积分?</h3>
        <ol start="1">
          <li>新手任务：新手用户完成引导任务，即可获得大量积分。</li>
          <li>每日任务：用户每天在重卡汇微信公众号签到、发帖、回帖、邀请朋友注册等均可获得积分，连续签到可获得额外积分，一定要记得每天来签到哦。</li>
          <li>购物：用户在重卡汇购买商品，确认收货后，可根据获得一定数量的积分。</li>
          <li>购物券转换：购买指定商品，如上汽红岩互联网重卡盲订券，在有效期截至后自动转换成重卡汇积分。</li>
        </ol>
      </div>
      <div class="item">
        <h3>如何使用重卡汇积分？</h3>
        <ol>
          <li>订单抵扣：根据100积分=1元的原则，客户可以在购买部分商品时使用积分，对订单金额进行抵扣。</li>
          <li>礼品兑换：客户可根据积分，在礼品兑换区换取礼品，每个种类数量有限，先到先得哦。</li>
          <li>大转盘抽奖：参与重卡汇举办的大转盘抽奖活动，50积分/次。</li>
        </ol>
      </div>
      <div class="item">
        <h3>重卡汇积分的使用规则有哪些？</h3>
        <ol>
          <li>积分可以累积，有效期至少为1年，即从获得开始至次年年底，逾期自动作废（如若交易在使用的积分有效期之内发生退款，该部分积分不予退还）；</li>
          <li>积分的数值精确到个位（小数点后全部舍弃，不进行四舍五入），例如：原价666.6元的商品，返666积分。</li>
          <li>买家在购买重卡汇商品时（不支持退现商品除外），在完成该笔交易后（重卡汇平台显示该交易状态为“交易成功”），才能得到此交易的相应积分；</li>
          <li>积分不能兑现，不可转让。</li>
          <li>买家在使用积分时，优先消耗旧积分（如买家积分由去年3月份和今年5月份共同累积，则优先消耗去年3月份的积分)。</li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
  import AppHeader from '../../../components/header/header'
  export default {
    components: { AppHeader }
  }
</script>

<style lang="less" scoped>
  .integral{
    padding-top: 40px;
    padding-bottom: 55px;
    background-color: #fff;
    h3 {
      margin: .5rem;
    }
    p {margin: 10px;}
  }
  ol, li {
    list-style: decimal;
  }
  ol {margin-right: 10px;}
</style>



